:root {
  --background: #000000 !important;
  --foreground: #f5f5dc !important;
  --primary-sand: #f5f5dc !important;
  --level-card-bg: rgba(245, 245, 220, 0.1) !important;
  --level-card-border: rgba(245, 245, 220, 0.3) !important;
}
body {
  background: linear-gradient(
    135deg,
    #000000 0%,
    #1a1a1a 50%,
    #2a2a2a 100%
  ) !important;
  color: var(--foreground) !important;
  font-family: var(--font-urbanist), var(--font-geist-sans), Arial, Helvetica,
    sans-serif !important;
  min-height: 100vh !important;
  overflow-x: hidden !important;
}
@keyframes float {
  0%,
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(-10px);
  }
}
@keyframes glow {
  0%,
  100% {
    box-shadow: 0 0 20px rgba(245, 245, 220, 0.3);
  }
  50% {
    box-shadow: 0 0 30px rgba(245, 245, 220, 0.5);
  }
}
@keyframes pulse {
  0%,
  100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}
.floating {
  animation: float 3s ease-in-out infinite;
}
.glowing {
  animation: glow 2s ease-in-out infinite;
}
.pulsing {
  animation: pulse 2s ease-in-out infinite;
}
@keyframes fadeInUp {
  0% {
    opacity: 0;
    transform: translateY(20px) scale(0.95);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}
@keyframes slideInFromBottom {
  0% {
    opacity: 0;
    transform: translateY(30px);
  }
  100% {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes pulseGlow {
  0%,
  100% {
    box-shadow: 0 0 20px rgba(245, 245, 220, 0.2);
  }
  50% {
    box-shadow: 0 0 40px rgba(245, 245, 220, 0.4);
  }
}
.fade-in {
  animation: fadeInUp 600ms ease-out both;
}
.slide-in {
  animation: slideInFromBottom 500ms ease-out both;
}
.glass-card {
  background: rgba(255, 255, 255, 0.08);
  backdrop-filter: blur(20px);
  border: 1px solid rgba(255, 255, 255, 0.1);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.1);
}
.hover-lift {
  transition: transform 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.hover-lift:hover {
  transform: translateY(-8px);
}
.gradient-text {
  background: linear-gradient(135deg, #f5f5dc, #d4af37, #b8860b);
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}
@keyframes roadFlow {
  0% {
    stroke-dashoffset: 0;
  }
  100% {
    stroke-dashoffset: 250;
  }
}
.roadway-line {
  animation: roadFlow 2s linear infinite;
}
::-webkit-scrollbar {
  width: 8px;
}
::-webkit-scrollbar-track {
  background: rgba(0, 0, 0, 0.2);
}
::-webkit-scrollbar-thumb {
  background: rgba(245, 245, 220, 0.3);
  border-radius: 4px;
}
::-webkit-scrollbar-thumb:hover {
  background: rgba(245, 245, 220, 0.5);
}
html,
body {
  background: linear-gradient(
    135deg,
    #000000 0%,
    #1a1a1a 50%,
    #2a2a2a 100%
  ) !important;
}
.bg-blue-500,
.bg-blue-600,
.bg-blue-700,
.bg-blue-800,
.bg-blue-900 {
  background-color: #000000 !important;
}
